export function random(from, to_ = from) {
    if (to_ === undefined) {
        to_ = from;
        from = 0;
    }
    if (to_ < from)
        [from, to_] = [to_, from];
    return Math.floor((Math.random() * ((to_ - from) + 1)) + from);
}
export function getStackStrace() {
    try {
        throw new Error();
    }
    catch (e) {
        return e.stack.split('\n').slice(2);
    }
}
