const randomBlock = () => Number(Math.random().toString().substring(2)).toString(16).substring(0, 8);
export function guid(randomBlocks = 2) {
    const datePart = (Date.now() * 3).toString(16);
    const timeZone = new Date().getTimezoneOffset();
    const timeZonePart = Number(`${timeZone < 0 ? "7" : "6"}${Math.abs(timeZone)}`).toString(16);
    const outputSize = (randomBlocks * 9) + 3 + 15;
    let output = "";
    for (let i = 0; i < randomBlocks; i++) {
        output += `${randomBlock()}-`;
    }
    output += timeZonePart + datePart;
    while (output.length < outputSize)
        output += randomBlock();
    return output.substring(0, outputSize);
}
let guidIndex = 0;
export function shortGuid() {
    return String(Date.now() * 3) + (guidIndex++);
}
export function getElementHash(container, element) {
    if (!container || !element)
        return null;
    const getIndex = (node) => {
        let index = 0;
        let sibling = node;
        while ((sibling = sibling.previousElementSibling)) {
            if (sibling.tagName === node.tagName)
                index++;
        }
        return index;
    };
    const path = [];
    let current = element;
    while (current && current !== container) {
        if (!current.tagName)
            return null;
        path.unshift(`${current.tagName}:${getIndex(current)}`);
        current = current.parentElement;
    }
    if (current !== container)
        return null;
    const pathString = path.join('|');
    let hash = 5381;
    for (let i = 0; i < pathString.length; i++) {
        hash = ((hash << 5) + hash) + pathString.charCodeAt(i);
        hash = hash & 0xFFFFFFFF;
    }
    return "h" + hash.toString(36);
}
